#root, body, html {
  height: 100%;
  min-height: 100%;
  font-size:12px;
  background-color: #f5f5f5;
  margin:0;
  padding:0;
}

h1 {
  font-size: 2rem;
  opacity: 0.8;
  font-weight: 700;
}

.container {
  -webkit-box-shadow: 0px 0px 24px -4px rgba(170,170,170,1);
  -moz-box-shadow: 0px 0px 24px -4px rgba(170,170,170,1);
  box-shadow: 0px 0px 24px -4px rgba(170,170,170,1);
  padding:1rem;
  margin:0.5rem;
  border: 1px solid #cccccc;
  border-radius: 0.5rem;
  width:calc(100% - 22px) !important;
  max-width: calc(100% - 22px) !important;
  background-color: #fafafa;
}

.App {
  height: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;

}

.App-header {
  background-color: #f9cd48;
  height: 70px;
  min-height: 70px;
  color: white;
}

.App-body {
  width:1600px;
}

.App-footer {
  background-color: darkgray;
  height: 50px;
}


.App-content {
  width:1400px;
  display: flex;
  flex-direction: row;
  height: 100%;
  min-height: 100%;
  align-items: stretch;
}

.App-body-sidebar {
  width:200px;
  flex-grow: 1;
  border-right: 1px solid grey;
  background-color: lightgray;
  padding:10px;
  height: 100%;
  min-height: 100%;
}

.App-body-sidebar ul {
  list-style: none;
}

.App-body-sidebar ul li {
  margin-top:25px;
}

.App-body-sidebar ul li a {
  color:black;
  text-decoration: none;
}

.App-body-sidebar ul li span {
  margin-left:25px;
}

.App-body-content {
  flex-grow: 9;
  height: 100%;

  min-height: 100%;
}



.App-link {
  color: #61dafb;
}

li.active a > span {
  font-weight: bold;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

tr:hover {
  cursor: pointer;
}

.menu-bar {
  background-color: whitesmoke;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-end;

}

div.spinner {
  position:fixed;
  width:100%;
  left:0;right:0;top:0;bottom:0;
  background-color: rgba(255,255,255,0.7);
  z-index:9999;  /*background-color: cyan;*/
}

@-webkit-keyframes spin {
  from {-webkit-transform:rotate(0deg);}
  to {-webkit-transform:rotate(360deg);}
}

@keyframes spin {
  from {transform:rotate(0deg);}
  to {transform:rotate(360deg);}
}

div.spinner::after {
  content:'';
  display:block;
  position:absolute;
  left:48%;top:40%;
  width:40px;height:40px;
  border-style:solid;
  border-color:black;
  border-top-color:transparent;
  border-width: 4px;
  border-radius:50%;
  -webkit-animation: spin .8s linear infinite;
  animation: spin .8s linear infinite;
}


.table-container {
  display: flex;
  flex-flow: column nowrap;
  background-color: white;
  width: calc(100% - 2rem) !important;
  margin: 0 auto;
  border-radius: 4px;
  border: 1px solid #DADADA;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, .08);
}

.table-row {
  display: flex;
  flex-flow: row nowrap;
  width: 100%;
  border-bottom: 1px solid #dadada;
}

.table-row:hover {
  /*cursor: pointer;*/
  background-color: #F0F0F0;
  /*   box-shadow: 0px 1px 4px rgba(0, 0, 0, .08); */
}

.heading {
  background-color: #ececec;
  color: #3e3e3e;
  font-weight: bold;
}

.row-item {
  display: flex;
  flex: 1;
  font-size: 12px;
  padding: 2px 8px;
  justify-content: flex-start;
  align-items: center;
  transition: all 0.15s ease-in-out;
}

.row-item:hover {
  cursor: pointer;
  /*background-color: #F0F0F0;*/
  /*   box-shadow: 0px 1px 4px rgba(0, 0, 0, .08); */
}

.row-sub-container {
  display: flex;
  flex-flow: column nowrap;
  flex: 1;
}

.row-sub-container .row-item {
  /*padding: 8px 0;*/
  border-bottom: 1px solid #dadada;
}

.table-row:last-child,
.row-sub-container .row-item:last-child {
  border-bottom: 0;
}