.form-field-row {
  width:100%;
  display: block;
  margin-bottom:3px;
}

.form-field-row label {
  display: block;
  font-size: 0.9rem;
  font-weight: 600;
  opacity: 0.7;
}

.form-field-row p {
  color: darkred;
  margin: 0;
  padding: 0;
  font-size: 0.9rem;
  font-weight: 600;
  opacity: 0;
}
.form-field-row p.error {
  opacity: 0.7;
}


.form-field-input {
  margin:0;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: .25rem;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  font-size: 0.8rem;
  padding: 3px 3px;
}

.form-field-input:hover, .form-field-row input:hover {
  background-color: white;
}

.form-field-input:disabled, .form-field-row input:disabled {
  background-color: #E9E9E9;
}




.form-field-input-label {
  display: inline-block;
  margin-right: 10px;
  text-align: left;
  font-weight: normal;
  font-size: 0.8rem;
  width: 100%;
}

.form-field-input--xs {
}
.form-field-input--sm {
}
.form-field-input--md {
  width:100%;
}
.form-field-input--lg {
}
.form-field-input--xl {
}

